import {Core, Fetch, ToastService} from '@true/core';
import {batch} from 'react-redux';
import {ISaved} from '@/store/saved/type';
import {setSavedQuery, syncSaved} from '@/store/saved/index';

export const requestSavedItems = async () => {
  const {data} = await Fetch.postWithToken<{
    saved: ISaved[];
  }>(`@${Core.systemPath}/inside/ajax/api/profile/get?tail=saved`);
  batch(() => {
    if (data.saved) {
      syncSaved(data.saved);
      setSavedQuery(prev => ({
        ...prev,
        ['all']: data.saved.map(item => item.id.toString()),
      }));
    }
  });
};

export const requestToggleSaved = async (params: {
  hid: string;
  status: 1 | -1;
}) => {
  const {data} = await Fetch.postWithToken<{
    obj: ISaved;
    saved: number;
  }>(`@${Core.systemPath}/inside/ajax/api/pref/save`, params);
  batch(() => {
    if (data.saved) {
      setSavedQuery(prev => ({
        ...prev,
        ['all']: [...prev.all.filter(id => id !== data.obj.id.toString())],
      }));
      ToastService.show('Saved successfully');
    }
  });
  return data.saved;
};
